import React, { Component } from 'react';
import AssetsTypes from './assetsType';
import Collections from './collections';
import Sources from './sources';
import UgcRights from './ugcrights';
import Tags from './tags';



class Filters extends Component {
    render() {

        //collection  source assetsType tags ugcrights
        const { filterKey } = this.props
        console.log("this.propsthis.propsthis.propsthis.props", this.props)
        return <>
            {filterKey == "collection" ? <Collections {...this.props} />
                : filterKey == "source" ? <Sources {...this.props} />
                    : filterKey == "assetsType" ? <AssetsTypes {...this.props} />
                        : filterKey == "tags" ? <Tags {...this.props} />

                            : filterKey == "ugcrights" ? <UgcRights {...this.props} /> : null
            }

        </>
    }
}

export default Filters